.App {
  /* text-align: center; */
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  display: block;
  width: 400px;
}

.Container-welcome {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Logo {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-right: 10px;
}

.Photo-circle {
  width: 25px;
  height: 25px;
  float: right;
}

.Text-title {
  display: flex;
  color: black;
  font-size: 15px;
}

.Container-hero {
  margin-top: 20px;
  width: 100%;
  border-radius: 10px;
  background-color: rgb(233, 255, 181);
}

.Container-menu-buttons {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 20px;
  width: 100%;
}

.Container-button-menu {
  display: flex;
  flex-direction: column;
  width: 25%;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
}

.Container-button-menu b {
  margin-top: 10px;
  color: black;
  font-size: 10px;
}

.Button-menu {
  display: flex;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: rgb(255, 225, 192);
}

.Border-line {
  width: 100%;
  height: 2px;
  background-color: rgb(207, 207, 207);
}

.Text-promo {
  display: flex;
  color: black;
  font-size: 25px;
}

.Container-promo-buttons {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 20px;
  width: 100%;
}

.Container-button-promo {
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
}

.Container-button-promo b {
  margin-top: 10px;
  color: black;
  font-size: 15px;
}

.Button-promo {
  display: flex;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  background-color: rgb(255, 225, 192);
}

.Navbar-bottom {
  width: 400px;
  height: 70px;
  background-color: white;
  position: sticky;
  bottom: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  border-top: 2px solid rgb(238, 238, 238);
}

.Navbar-button-menu {
  display: flex;
  flex-direction: column;
  width: 25%;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}

.Navbar-button-menu b {
  margin-top: 10px;
  color: rgb(110, 122, 156);
  font-size: 10px;
  margin-bottom: 10px;
}

.Navbar-menu {
  display: flex;
  width: 25px;
  height: 25px;
}

